import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RendidorPage } from './rendidor.page';

const routes: Routes = [
  {
    path: 'rendidor',
    component: RendidorPage,
    children: [
      {
        path: 'gastos',
        children: [
          {
            path: '',
            loadChildren: () =>
              import('./gastos/gastos.module').then(m => m.GastosPageModule)
          }
        ]
      },
      {
        path: 'informe',
        children: [
          {
            path: '',
            loadChildren: () =>
              import('./informe/informe.module').then(m => m.InformePageModule)
          }
        ]
      },
      {
        path: 'galeria',
        children: [
          {
            path: '',
            loadChildren: () =>
              import('./galeria/galeria.module').then(m => m.GaleriaPageModule)
          }
        ]
      },
      {
        path: '',
        redirectTo: 'gastos',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: '',
    redirectTo: '/rendidor/gastos',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RendidorPageRoutingModule {}
