import { Component } from '@angular/core';
import { PlatformService } from '../services/platform.service';
import { PopoverController } from '@ionic/angular';
import { PerfilPopover } from 'src/app/perfil/perfil.component';

export interface RouteInfo {
  path: string;
  title: string;
  iconType: string;
  icon: string;
  badge?: number;
}

export const ROUTESRENDIDOR: RouteInfo[] = [
  {
    path: 'gastos',
    title: 'Gastos',
    iconType: 'custom',
    icon: 'assets/icons/receipt-solid.svg',
    badge: 2
  },
  {
    path: 'informe',
    title: 'Informes',
    iconType: 'custom',
    icon: 'assets/icons/file-invoice-dollar-solid.svg',
    badge: 2
  },
  {
    path: 'galeria',
    title: 'Galeria',
    iconType: 'custom',
    icon: 'assets/icons/images-solid.svg',
    badge: 4
  }
];

@Component({
  selector: 'app-rendidor',
  templateUrl: 'rendidor.page.html'
})

export class RendidorPage {
  menus;
  title = '';
  tab = '';
  placement;
  constructor(private pltService: PlatformService, public popoverController: PopoverController) {
    console.log(this.pltService.getPlatformTabs());
    this.placement = this.pltService.getPlatformTabs();
    this.menus = ROUTESRENDIDOR;
  }


  changeTab(e) {
    this.tab = e.tab;
    this.title = e.tab.charAt(0).toUpperCase() + e.tab.slice(1);
  }


  async popoverPerfil(ev: any) {
    const popover = await this.popoverController.create({
      component: PerfilPopover,
      event: ev,
      translucent: true,
      cssClass: 'custom_popover',
      mode: 'ios'
    });
    return await popover.present();
  }

}
